import React, { useEffect } from 'react';
import {Box, Button, FormControl, FormControlLabel, Grid, InputAdornment, InputLabel, makeStyles, MenuItem, Radio} from "@material-ui/core"
import { ErrorMessage, Field, Formik, Form } from "formik";
import {RadioGroup, Select, TextField} from "formik-material-ui";
import {AccountCircle, EmailRounded, EventRounded, HomeRounded, LabelImportantRounded, PhoneRounded} from "@material-ui/icons";
import {DatePicker} from "formik-material-ui-pickers";
import * as Yup from "yup";
import DateView from "react-datepicker";
import axios from "axios";
import "react-datepicker/dist/react-datepicker.css";

import "../App.css";
import TextError from "./TextError";
import { Link } from 'react-router-dom';

const initialValues = {
    age: 0,
    city: "",
    colony: "",
    dob: null,
    emailID: "",
    gender: "",
    house: "",
    id: "",
    idNumber: "",
    lane: "",
    location: "Location denied",
    mobileNumber: "",
    nagarNigam: "",
    name: "",
    sampleMode: "",
    slot: "",
    wardNumber: "",
};
const onSubmit = (values, onSubmitProps) => {
    // getLocation();
    // axios({
    //     method: "post",
    //     url: "localhost:5000/bookings",
    //     data: values,
        
    // })
    axios.post("/bookings", values).then(res => console.log(JSON.parse(res.config.data),res, "=========res")).catch(err => console.log(err));
    // console.log(values,"==========submit");
    onSubmitProps.resetForm();
    alert("Form Submitted");
};

// const validate = values => {
//     let errors = {};
//     if (!values.name) errors.name = "Required";
//     if (!values.idNumber) errors.idNumber = "Required";
//     if (!values.id) errors.id =  "Required";
//     return errors;
// };
const validationSchema = Yup.object({
    id: Yup.string().required("Required"),
    idNumber: Yup.string().required("Required").when("id", {
        is: "aadhar",
        then: Yup.string().matches(/^[2-9]{1}[0-9]{3}\s?[0-9]{4}\s?[0-9]{4}$/, "Must be 12 digits"),
        otherwise: Yup.string().matches(/^[A-PR-WYa-pr-wy][1-9]\d\s?\d{4}[1-9]$/, "Invalid format")
    }),
    name: Yup.string().required("Required"),
    slot: Yup.string().required("Required"),
    gender: Yup.string().required("Required"),
    nagarNigam: Yup.string().required("Required"),
    house: Yup.string().required("Required"),
    lane: Yup.string().required("Required"),
    colony: Yup.string().required("Required"),
    city: Yup.string().required("Required"),
    sampleMode: Yup.string().required("Required"),
    dob: Yup.date().required("Required").nullable(),
    emailID: Yup.string().email("Invalid email format").required("Required"),
    wardNumber: Yup.number().positive().integer().min(10).max(999),
    mobileNumber: Yup.number().required("Required").test("len", "Must be 10 digits", val => val && val.toString().length === 10)
});
const sampleOptions = [
    {key: "Home Collection", value: "home"},
    {key: "Drive-in", value: "drive"}
];
const idOptions = [
    {key: "Aadhar Card", value: "aadhar"},
    {key: "Passport", value: "passport"}
];
const genderOptions = [
    {key: "MALE", value: "male"},
    {key: "FEMALE", value: "female"},
    {key: "OTHERS", value: "other"}
]
const nnOptions = [
    {key: "Heritage, Jaipur", value: "heritage"},
    {key: "Greater, Jaipur", value: "greater"}
]

function getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.watchPosition(showPosition);
    }
}

function showPosition(position) {
    initialValues.location ="Latitude: " + position.coords.latitude + 
    "Longitude: " + position.coords.longitude;
}

const useStyles = makeStyles((theme) => ({
    paper: {
    //   marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(3),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    wrap: {
        padding: "0 16px"
    }
  }));

// function getLocation() {
//     function success(position) {
//         const latitude  = position.coords.latitude;
//         const longitude = position.coords.longitude;
//         var mapLink = `Latitude: ${latitude} °, Longitude: ${longitude} °`;
//         return mapLink;
//     }
//     function error() {
//         return "User denied location permission";
//     }
//     initialValues.location = navigator.geoLocation.getCurrentPosition(success, error);
// };
// if (window.navigator.geolocation) getLocation();

function BookingForm() {
    const classes = useStyles();

    useEffect(() => {
        getLocation();
        document.title = "RT-PCR Booking";
        document.getElementsByTagName('meta')["description"].content = "RT-PCR Test Booking Form";
    }, []);
    
    return (
        <div className={classes.paper}>
        <div style={{backgroundColor:"#3f51b5", color:"white", textAlign:"center", marginBottom: "1em"}}>
            <h2>Please fill all the details</h2>
        </div>
        <div className={classes.wrap}>
        <Formik className={classes.form} initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
            {(formik) => (
                <Form>
                    <div className="form-control">
                        <label className="label">Mode of Sample Collection:</label>
                        <Field name="sampleMode">
                            {({field}) => {
                                return sampleOptions.map(option => (
                                    // <FormControlLabel key={option.value} value={option.value} control={<Radio />} label={option.key} />
                                    <label className="Radio" key={option.value}>
                                        <input className="Radio-Input" type="radio" id={option.value} {...field} value={option.value} checked={field.value === option.value} />
                                        {option.key}
                                    </label>
                                ))
                            }}
                        </Field>
                        <div className="error">
                            {formik.values.age < 65 && formik.values.sampleMode === "home" ? formik.errors.sampleMode = "Must be 65 years or above" : ""}
                        </div>
                        <ErrorMessage component={TextError} name="sampleMode" />
                    </div>
                    <div className="form-control">
                        <label className={"label"}>Slot Availability:</label>
                        Available for the next day-
                        <Field component={Select} name="slot" inputProps={{id: "slot"}} >
                            <MenuItem value="">Pick a Slot</MenuItem>
                            <MenuItem value="10-10.3">10:00 - 10:30</MenuItem>
                            <MenuItem value="12-12.3">12:00 - 12:30</MenuItem>
                            <MenuItem value="13-13.3">13:00 - 13:30</MenuItem>
                            <MenuItem value="14-14.3">14:00 - 14:30</MenuItem>
                            <MenuItem value="15-15.3">15:00 - 15:30</MenuItem>
                        </Field>
                        <ErrorMessage component={TextError} name="slot" />
                    </div>
                    <div className="form-control">
                        <label className="label">
                            ID Proof:
                        </label>
                        <Field component={TextField} name="id">
                            {({field}) => {
                                return idOptions.map(option => (
                                    <label className="Radio" key={option.value}>
                                        <input className="Radio-Input" type="radio" id={option.value} {...field} value={option.value} checked={field.value === option.value} />
                                        {option.key}
                                    </label>
                                ))
                            }}
                        </Field>
                        <ErrorMessage component={TextError} name="id" />
                    </div>
                    <div className="form-control rest">
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <LabelImportantRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" label="ID Proof Number" name="idNumber" />
                    </div>
                    <div className="form-control">
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <AccountCircle color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" label="Name" name="name" />
                    </div>
                    <div className="form-control">
                        {/* <label>Date of Birth:</label> */}
                        <Field name="dob">
                            {({field, form}) => (
                                <DateView className="date rest" placeholderText="Date of Birth" maxDate={new Date()} showYearDropdown dropdownMode="select" {...field} selected={field.value} onChange={val => {form.setFieldValue("dob", val); form.setFieldValue("age", ~~((Date.now() - +val) / 31557600000))}} />
                            )}
                        </Field>
                        {/* {console.log(formik, "below date")} */}
                        <ErrorMessage component={TextError} name="dob" />
                    </div>
                    <div className="form-control">
                        <label className="label">Gender:</label>
                        <Field component={TextField} name="gender">
                            {({field}) => {
                                return genderOptions.map(option => (
                                    <label className="Radio" key={option.value}>
                                        <input className="Radio-Input" type="radio" id={option.value} {...field} value={option.value} checked={field.value === option.value} />
                                        {option.key}
                                    </label>
                                ))
                            }}
                        </Field>
                        <ErrorMessage component={TextError} name="gender" />
                    </div>
                    <div className="form-control">
                        <Field className="num" component={TextField} variant="outlined" label="Mobile Number" 
                            InputProps={{startAdornment: (<InputAdornment position="start"><PhoneRounded color="primary" />+91</InputAdornment>)
                            }} type="number" id="mobileNumber" name="mobileNumber" />
                    </div>
                    <div className="form-control">
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <EmailRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} label="Email ID" type="email" variant="outlined" name="emailID" />
                    </div>
                    <div className="form-control">
                        <label className="label">Address:</label>
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <HomeRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" placeholder="House Number" name="house" />
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <HomeRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" placeholder="Lane" name="lane" />
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <HomeRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" placeholder="Colony" name="colony" />
                        <Field component={TextField} InputProps={{
                            startAdornment: (
                            <InputAdornment position="start">
                                <HomeRounded color="primary" />
                            </InputAdornment>
                            ),
                        }} variant="outlined" placeholder="City with PIN Code" name="city" />
                    </div>
                    <div className="form-control">
                        <Field className="rest" component={TextField} label="Ward Number" variant="outlined" type="number" id="wardNumber" name="wardNumber" />
                    </div>
                    <div className="form-control">
                        <label className="label">Nagar Nigam:</label>
                        <Field component={TextField} name="nagarNigam">
                            {({field}) => {
                                return nnOptions.map(option => (
                                    <label className="Radio" key={option.value}>
                                        <input className="Radio-Input" type="radio" id={option.value} {...field} value={option.value} checked={field.value === option.value} />
                                        {option.key}
                                    </label>
                                ))
                            }}
                        </Field>
                        <ErrorMessage component={TextError} name="nagarNigam" />
                    </div>
                    {/* <Button style={{marginBottom: "1em"}} fullWidth variant="contained" color="primary" onClick={getLocation}>Share Current Location</Button> */}
                    <Grid style={{margin: "1em 0"}} container justify="center" >
                        <Button style={{margin: "0 1em"}} type="submit" variant="contained" color="primary" disabled={!formik.isValid}>
                            Submit
                        </Button>
                        <Link to="/" style={{textDecoration:"none"}}>
                        <Button variant="contained" color="primary">
                            Go Back
                        </Button>
                        </Link>
                    </Grid>
                </Form>
            )}
        </Formik>
        </div>
        </div>
    )
}

export default BookingForm
